import React from 'react';

class Filters extends React.PureComponent {
  render() {
    const sortButton           = this.props.isAsc            ? 'fa-caret-square-up' : 'fa-caret-square-down';
    const bookButtonMode       = this.props.showBooks        ? '' : 'txtRed';
    const comicButtonMode      = this.props.showComics       ? '' : 'txtRed';
    const movieButtonMode      = this.props.showMovies       ? '' : 'txtRed';
    const dateButtonMode       = this.props.showDate         ? '' : 'txtRed';
    const showDuplicatesButton = (this.props.showDuplicates) ? ('[X]') : ('[ ]');

    return (
      <div className="flex flex-col m-flex-row spaceBetween alignCenter mb5 fs14">
        <div className="flex alignCenter mr5 mb5 m-mb0 flex100 m-flex1">
          <i className={`mr5 fas ${sortButton} csrPointer`} onClick={this.props.toggleIsAsc}></i>
          <i className={`mr5 fas ${bookButtonMode} fa-book csrPointer`} onClick={this.props.toggleShowBooks}></i>
          <i className={`mr5 fas ${comicButtonMode} fa-book-open csrPointer`} onClick={this.props.toggleShowComics}></i>
          <i className={`mr5 fas ${movieButtonMode} fa-film csrPointer`} onClick={this.props.toggleShowMovies}></i>
          <i className={`mr5 fas ${dateButtonMode} fa-calendar-check csrPointer`} onClick={this.props.toggleShowDate}></i>
          <span
          className="mr5 bold csrPointer"
          onClick={this.props.toggleShowDuplicates}
          >
            {showDuplicatesButton}
          </span>
        </div>
        <div className="flex flex100 m-flex1">
          <div className="mr5 flex33 m-w100">
            <input
            className="bdrBox bdrGray p5 flex100"
            id="dateStart"
            name="dateStart"
            onChange={this.props.filterDate}
            placeholder="from: mmddyy"
            value={this.props.dateStart}
            />
          </div>
          <div className="mr5 flex33 m-w100">
            <input
            className="bdrBox bdrGray p5 flex100"
            id="dateEnd"
            name="dateEnd"
            onChange={this.props.filterDate}
            placeholder="to: mmddyy"
            value={this.props.dateEnd}
            />
          </div>
          <div className="flex33 m-w100">
            <input
            className="bdrBox bdrGray p5 flex100"
            name="title"
            onChange={this.props.filterTitle}
            placeholder="title"
            value={this.props.title}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Filters;
